import React, { Component } from 'react';
import Vids from './../video-modal/videos';
import VideoItem from './../video-modal/video';

class VideosPage extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            filterMode: 'NONE',
            viewBy: 'GAME'
         }
    }

    getAllVids = () => {
        
        let t = [];

        for (let games of Vids.games) {

            t = t.concat(games.videos);

        }

        return t;
    }

    filterVids = () => {

        let vids = this.getAllVids();

        switch (this.state.filterMode) {
            case 'TRAILER':
                return vids.filter((v, i) => {
                    return v.CATEGORY === 2
                });
            case 'INTRO':
                return vids.filter((v, i) => {
                    return v.CATEGORY === 3
                });
            case 'ECOLOGY':
                return vids.filter((v, i) => {
                    return v.CATEGORY === 4
                });
            default:
                return vids;
        }   
    }

    setFilter = (filter_mode) => {
        if (this.state.viewBy === 'GAME')
            this.setState({
                filterMode: filter_mode,
                viewBy: 'CATEGORY'
            });
        else if (filter_mode === 'NONE')
            this.setState({
                filterMode: filter_mode,
                viewBy: 'GAME'
            });
        else
            this.setState({
                filterMode: filter_mode
            });
    }

    renderByCategory = () => { 

        let v = this.filterVids();
        let renderVids;

        if (v.length < 1)
            renderVids = ( 
                <div className = "error empty">
                    No Videos Available
                </div> 
            );
        else
            renderVids = this.filterVids().map( (v, i) => {
                    return ( <VideoItem key = { i } video = { v } /> );
                });

        return ( 
            <div className = "videos-wrapper">
                <h3>
                    { this.state.filterMode }
                </h3>
                <div className = "videos-inner">
                    { renderVids }
                </div>
            </div>
        );
    }

    renderButtons = () => {

        return Vids.categories.map( (c, i) => {
            return ( 
                <button key = { i } onClick = { this.setFilter.bind(this, c.CATEGORY) }>
                    { c.CATEGORY }
                </button>
            )
        });
    }

    renderByGame = () => {
        let renderCont = Vids.games.map((g, i) => {

            let v = g.videos.map( (v, j) => {

                return ( <VideoItem key = { j } video = { v } /> );

            } );

            return (

                <div key = { g.id } className = "game-videos">
                    <h3>{ g.name }</h3>

                    { v }
                </div>

            )

        });

        return (
            <div className = "videos-wrapper">
                <div className = "videos-inner">
                    { renderCont }
                </div>
            </div>
        );
    }

    render () {
        return (
            <div className = "videos-list">
                <div className = "controls-wrapper">
                   <span>Sort By</span> 
                   { this.renderButtons() }
                </div>
                { (this.state.viewBy === 'CATEGORY') ? this.renderByCategory() : this.renderByGame() }
            </div>
        )
    }
}
 
export default VideosPage;